<template>
  <div class="tiktok-succ">
    <div class="tiktok-succ__bg"></div>
    <div class="tiktok-succ__content">
      <!-- 领取成功 -->
      <img
        src="@/assets/tiktokSuccess/img1.png"
        alt=""
        class="tiktok-succ__img1">
      <!-- 关注孙老师 -->
      <img
        src="@/assets/tiktokSuccess/img2.png"
        alt=""
        class="tiktok-succ__img2">
      <!-- 复制按钮 -->
      <div class="tiktok-succ__img3-box">
        <div class="tiktok-succ__img3__bg">
          <img
            src="@/assets/tiktokSuccess/img3_btn.png"
            alt=""
            class="tiktok-succ__img3-btn"
            @click="copy">
        </div>
      </div>
      <!-- 步骤展示 -->
      <img
        src="@/assets/tiktokSuccess/img4.png"
        alt=""
        class="tiktok-succ__img4">
      <!-- 动图展示 -->
      <div class="tiktok-succ__img5-box">
        <img
          src="@/assets/tiktokSuccess/img6.gif"
          alt=""
          class="tiktok-succ__img6">
      </div>
    </div>

  </div>
</template>

<script>
import { copyText } from '@/utils/index.js'
import { clickLog } from '@/helper/clickLog'
import { Dialog } from 'vant'
import { getSearch } from '@/utils'
export default {
  name: 'TiktokSuccess',
  methods: {
    /**
     * @description 一键复制
     */
    async copy () {
      try {
        copyText('孙老师在微淼')
        await this.$apis.Tiktok.quickCopyNoOrderNum({
          // medId: 1030,
          ...getSearch(),
          protocol: window.location.protocol,
          host: window.location.host,
          link: window.location.href
            .replace(window.location.protocol + '//', '')
            .replace(window.location.host, '')
        })
        window.location.href = 'weixin://'
      } catch (err) {
        const { data: { msg } } = err
        Dialog({ message: msg })
      } finally {
        clickLog('一键复制', 'gdt-lh-lzq-1015')
      }
    }
  }
}
</script>

