<template>
  <BaseModal :show="show">
    <div class="receive">
      <div class="btn" @click="toggleShow(1)"></div>
      <div class="close" @click="toggleShow(1)"></div>
    </div>
  </BaseModal>
</template>

<script>
import BaseModal from '@/components/BaseModal'
// import { clickLog } from '@/helper/clickLog'
export default {
  components: {
    BaseModal
  },
  data () {
    return {
      show: false
    }
  },
  methods: {
    /**
     * @description:切换弹框状态
     */
    toggleShow (type) {
      if (type === 1) {
        localStorage.setItem('tiktokModal', true)
      }
      this.show = !this.show
    }
  }
}
</script>

