<script>
import TiktokReceive from './modal/TiktokReceive'
export default {
  name: 'Tiktok',
  components: {
    TiktokReceive
  },
  created () {
    // 判断是否已经弹出过弹框
    if (localStorage.getItem('tiktokModal') === 'true') {
      return
    }
    // 打开弹窗
    this.$nextTick(function () {
      this.$refs.showTiktokReceive.toggleShow()
    })
  },
  methods: {
    /**
     * @description  立即领取
     */
    receive () {
      this.$router.push('/TiktokSuccess')
    }
  }
}
</script>

